import React from "react";
import "./NFTCSS.css";
import logo from "./logo_placeholder.png";
import "bootstrap/dist/css/bootstrap.min.css";
import { fireEvent } from "@testing-library/dom";
import { Route, Router, Link } from "react-router-dom";

const WalletLink = () => {
  return (
    <div>
      <h4 className="wallet">
        <a>Grab Your Wallet</a>
      </h4>
    </div>
  );
};

export function Header() {
  return (
    <div className="headerDiv">
      <img src={logo} className="logo img-responsive" alt="" />
      <WalletLink />
    </div>
  );
}

const LearnMore = () => {
  return (
    <Router>
      <div className="tourMain">
        <p className="learnMoreP">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Veniam
          quisquam vitae maxime doloribus. Inventore pariatur ullam eligendi
          voluptatem id culpa?
        </p>
        <div className="learnMoreDiv">
          {/*<h2 className="learnMoreBox">
          <Link className="learnMoreLink">Learn More</Link>
        </h2>*/}

          <h5>
            <Link to="home" className="skipLink">
              Skip
            </Link>
          </h5>
        </div>
      </div>
    </Router>
  );
};

export const NFTProject = () => {
  return (
    <div className="exportDiv">
      <Header />
      <LearnMore />
    </div>
  );
};
